import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Divider from '@material-ui/core/Divider';

import cssClasses from './PremiumServices.module.css';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
}));



export default function CenteredGrid() {
  const classes = useStyles();

  return (
    <Container className={cssClasses.container} fixed>
      <Grid container spacing={3}>
        <Grid item lg={4} md={6} sm={12}>
          <Paper className={classes.paper} elevation={3}>
            <Typography className={cssClasses.topicHeading} variant="h4" gutterBottom>
              Financial Empowerement Courses
              </Typography>
              <br />
            <Divider light />
            <Typography variant="body1" className={cssClasses.content} gutterBottom>
            Financially educate yourself with our fun and engaging animated videos which teaches you everything from what is inflation, what are the different strategies people use to invest in Stock market and in other markets like real estate, etc. Educate yourself about how to utilize money smartly and create a financial independent base for yourself. 
            </Typography>
          </Paper>
        </Grid>
        <Grid item lg={4} md={6} sm={12}>
          <Paper className={classes.paper} elevation={3}>
            <Typography className={cssClasses.topicHeading} variant="h4" gutterBottom>
              Invest in all investment avenues from Paisaat
              </Typography>
              <br />
            <Divider light />
            <Typography variant="body1" className={cssClasses.content} gutterBottom>
            Tired of using different platform for buying different asset class? Now worry no more, buy from our single platform , and the best part? You don’t have to pay different premium fees for buying different asset class from different platform. All in one investment platform – Paissat.
            </Typography>
          </Paper>
        </Grid>
        <Grid item lg={4} md={6} sm={12}>
          <Paper className={classes.paper} elevation={3}>
            <Typography className={cssClasses.topicHeading} variant="h4" gutterBottom>
              Track Investment and Investment Portfolio Management
              </Typography>
              <br />
            <Divider light />
            <Typography variant="body1" className={cssClasses.content} gutterBottom>
            Are you making profit or loss? Track your investment asset class and how it is performing. Also with our investment management portfolio, manage your investment bag and maintain liquidity so that you don’t lose a good investing opportunity . Manage your investment bag with debt based , equity based financial instruments or with gold and silver.
            </Typography>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
}
